import './displayBox.css';


const DisplayBox = (props:any) => {
  /*
  The display box component is used to display an image with a title using the 'img' & 'title' props.
  Alternatively you can also trigger a modal to appear when the user clicks the display by passing the
  react-state to the 'modal' prop like this modal={[var, setVar]} or even link to another page using
  the 'url' prop.
  */

  let _onclick = () => { return null; };

  if (props.callback !== undefined) {
    _onclick = () => props.callback();
  } else if (props.url !== undefined) {
    _onclick = () => window.location = props.url;
  }

  return <div className="display-box" onClick={_onclick}>
    <img className="no-select" src={props.img} alt={props.title}/>
    <p className="no-select">{props.title}</p>
  </div>

};


export default DisplayBox;
